:root {
	--mol_skin_base: hsl( 140 , 35% , 50% );
	--mol_skin_round: .25rem;
}

[mol_app_demo_optioner] {
	display: flex;
	flex-direction: column;
	padding: .5rem;
}

[mol_app_demo_mainer] {
	background: var(--mol_skin_light);
	overflow: auto;
}

[mol_app_demo_lister] {
	display: flex;
	flex-wrap: wrap;
	height: 100%;
	padding: .5rem;
	align-content: flex-start;
	align-items: flex-start;
}

[mol_app_demo_screen] {
	max-height: 45%;
}

[mol_app_demo_pager_bodier] {
	display: flex;
	align-items: flex-start;
	padding: 1rem;
}

[mol_app_demo_pager_backer] {
	color: var(--mol_skin_base_text);
	display: flex;
	align-items: center;
	margin-right: -1rem;
}

[mol_app_demo_pager_backer_icon] {
	transform: rotateZ(180deg);
}

[mol_app_demo_sample] {
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: flex-start;
}
